.App {
    text-align: center;
}

.App-header {
    background: url("./images/banner-min.jpg") center center / cover;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-header::after {
    transition: opacity 2.5s ease;
    transition-delay: .75s;
    -moz-pointer-events: none;
    -webkit-pointer-events: none;
    -ms-pointer-events: none;
    pointer-events: none;
    background: #000;
    background: linear-gradient(0, #000, #123 100%, #00d4ff 0);
    content: "";
    display: block;
    height: 100%;
    left: 0;
    opacity: .85;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.App-header .Inner {
    z-index: 100;
}

.App-header .Inner h1 {
    font-style: italic;
    font-weight: 400;
}

.App-header .Inner p {
    font-size: .7em;
    font-weight: 400;
    letter-spacing: .25em;
    text-transform: uppercase;
    font-style: italic;
}

.App-header .Inner h1,
.App-header .Inner p {
    -webkit-transition: 0.2s;
    -moz-transition: 0.2s;
    -ms-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.App-header .Inner h1:hover,
.App-header .Inner p:hover {
    color: #ca6702
}

.App-header .Inner .Telegram-link {
    display: block;
    margin-top: 3rem;
}

.App-header .Inner .Telegram {
    width: 48px;
    height: auto;
    -ms-filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.6));
    filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.6));
    -webkit-transition: 0.2s;
    -moz-transition: 0.2s;
    -ms-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.App-header .Inner .Telegram:hover {
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
    transform: scale(1.2);
}